import React from 'react'
import {createBrowserRouter} from 'react-router-dom'
import Home from '@/page/Home'
import WordPage from '@/page/WordDetail'

const router = createBrowserRouter([
    {
        path: "/",
        element: <Home/>,
    },
    {
        path:"/word",
        element:<WordPage/>
    }
]);

export default router

