import React from 'react';
import styles from './style.module.scss'
import {useNavigate} from "react-router-dom";
import {Card, Space} from "antd-mobile";
import data from '@/constant/wordList'

const Index = () => {
    const navigate = useNavigate()

    const handleClick = (val:any)=>{
        navigate('/word',{state:val})
    }

    return (
        <div className={styles.root}>
            <div className="header">
                考研6级单词速记
            </div>
            <Space className="space" direction="vertical">
                {
                    data.map((item:{title:string,video:string},index:number)=>{
                        return <Card onClick={()=>{handleClick(item)}} className="card" key={index}>{item.title}</Card>
                    })
                }
            </Space>
        </div>
    );
};

export default Index;
