import React, {useState} from 'react';
import styles from './style.module.scss'
import {useNavigate} from "react-router-dom";

const Index = (props: any) => {
    const navigate = useNavigate()
    const {title='标题', data='',back=true} = props
    const [isOpenVideo, setIsOpenVideo] = useState(false)
    const handleGoBack = () => {
        // @ts-ignore
        navigate('/', {
            replace: true
        })
    }

    const handleView = () => {
        setIsOpenVideo(!isOpenVideo)
    }

    return (
        <div className={styles.root}>
            <div className="bar">
                {back?<div className="back-btn" onClick={handleGoBack}>Back</div>:<div className="box"></div>}
                <div className="title">{title}</div>
                {data?<div className="back-btn orange" onClick={handleView}>{isOpenVideo?'Close':'Video'}</div>:<div className="box"></div>}
            </div>
            {data&&isOpenVideo && <video className="video"
                                   src={`https://october-x-image-host.oss-cn-hangzhou.aliyuncs.com/word/video/${data}.mp4`}
                                   controls={true}
            ></video>}
        </div>
    );
};

export default Index;
